@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --light: rgb(231, 231, 231);
  --medium: rgb(212, 206, 206);
  --dark: rgb(32, 38, 49);
  --blue: rgb(8, 139, 200);
  --blue-light: rgb(83, 169, 219);
  --red: rgb(200, 8, 8);
  --red-light: rgb(219, 83, 83);
}

.delete-item-button {
  width: 25px;
  height: 25px;
  padding: 0;
  background: rgb(200, 8, 8);
  background: var(--red);
  border: 2px solid rgb(219, 83, 83);
  border: 2px solid var(--red-light);
  color: white;
  font-size: 10px;
  border-radius: 5px;
  cursor: pointer;
}

#file-upload-button,
.edit-item-button {
  padding: 0px 10px;
  background: rgb(8, 139, 200) !important;
  background: var(--blue) !important;
  border: 2px solid rgb(83, 169, 219);
  border: 2px solid var(--blue-light);
  color: white;
  font-size: 15px;
  border-radius: 5px;
  cursor: pointer;
}

footer {
  height: 60px;
  text-align: center;
  line-height: 60px;
  background: black;
  color: wheat;
  font-family: 'Poppins', sans-serif;
}
.navbar {
  background: rgb(235, 239, 243);
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  margin-left: 20px;
  font-size: 18px;
  color: rgb(12, 12, 12);
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 800;
  position: relative;
  top: -2px;
}


.logo span {
  font-size: 44px;
  font-weight: 800;
  color: rgb(27, 202, 27);
}
.logo span:nth-child(1) {
  position: relative;
  right: -3px;
}

.nav-button {
  width: 120px;
  height: 40px;
  background: rgb(200, 8, 8);
  border: 2px solid rgb(219, 83, 83);
  color: white;
  font-size: 20px;
  border-radius: 5px;
  margin-right: 20px;
}

.nav-button:nth-child(2) {
  background: rgb(8, 139, 200);
  border: 2px solid rgb(26, 209, 233);
}


.check-button, .add-item-button {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background: rgb(8, 139, 200);
  border: 2px solid rgb(26, 209, 233);
  border-radius: 5px;
  padding: 5px;
  margin-left: 10px;
  color: #fff;
}

textarea {

  border-radius: 5px;
  font-size: 15px ;
  font-family: 'Poppins', sans-serif;
  color: var(--dark);
  line-height: 18px;
}

.header {
  width: 100%;
  height: 250px;
  background: var(--dark);
  padding-top: 100px;
  text-align: center;
}

.name, .position {
  font-family: 'Russo One', sans-serif;
  font-size: 32px;
  text-transform: uppercase;
  color: #fff;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
}

.position {
    position: relative;
    font-size: 1.2em;
    letter-spacing: 2px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 0 5px;
    margin: 0 auto;
    font-weight: 600;
    background: var(--light);
    z-index: 2;
    color: var(--dark);
    text-transform: uppercase;
}

.position-wrap {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  align-items: center;
  margin: 0 auto; 
}

.dash-on-position {
  width: 30px;
  height: 4px;
  background: var(--light);
  margin: 0 20px;
}

.sideInfo {
  padding: 23px 23px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: var(--medium);
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  width: 290px;
  color: var(--dark);
}

ul {
  margin-bottom: 55px;
  /* align-self: flex-start; */
}
ul li {
  list-style: none;
  font-size: 15px;
  font-family: 'Poppins', sans-serif;
  border-bottom: 1px solid rgba(32, 38, 49, 0.2);
  padding: 5px 0;
  width: 247px;
  display: flex;
  flex-wrap: wrap;
}


.fakeBlankSpace {
  display: inline-block;
  opacity: 0;
}

.listText { 
  margin-left: 0px;
  opacity: 0.8;
  word-break: break-all;
}

.link {
  position: relative;
  font-size: 15px;
  font-family: 'Poppins', sans-serif;
  border-bottom: 1px solid rgba(32, 38, 49, 0.2);
  padding: 5px 0;
  width: 247px;
  display: flex;
  flex-wrap: wrap;
}

.link .delete-item-button,
.link .edit-item-button  {
  position: absolute;
  right: 0;
  top: 10px;
  display: none;
}

.link .edit-item-button {
  right: 30px;
}

.links-list .add-item-button {
  margin: 0 auto;
  margin-top: 15px;
  display: none;
}

.links-list:hover .add-item-button,
.link:hover .delete-item-button,
.link:hover .edit-item-button {
  display: block;
}


.links-list  .link svg {
  position: relative;
  top: 0px;
  width: 30px;
  height: 30px;
  fill: var(--dark);
}
.links-list  .link a {
  position: relative;
  top: 7px;
  color: var(--dark);
  margin-left: 48px;
}


.image img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  margin-bottom: 30px;
}

.image {
  position: relative;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  margin-bottom: 10px;
  background: url(data:image/png;base64,) center top /cover;
}



.custom-file-button {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 2px 5px;
  background: var(--blue) !important;
  border: 2px solid var(--blue-light);
  color: white;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  display: none;
}

img:hover  .custom-file-button,
.image:hover  .custom-file-button {
  display: block;
}

.image input {
  display: none !important;
  visibility: hidden;
  width: 0px;
  height: 0px;
  position: absolute;
  left: 1999px;
}


.add-link-icon {
  width: 25px;
  height: 25px;
  background: transparent;
  border: none;
  margin: 15px;
}

.add-link-icon svg {
  fill: var(--dark);
  pointer-events: none;
}

.addLinkTable {
  position: relative;
  background: var(--light);
  width: 250px;
  border: 2px solid var(--blue);
  display: flex;
  flex-wrap: wrap;
  padding: 30px 10px;
  margin-top: 20px;
  border-radius: 15px;
}
.addLinkTable .delete-item-button {
  position: absolute;
  top: 5px;
  right: 5px;
}
.about-me-header {
  margin-top: -10px;

}

section {
  background: var(--light);
  padding: 20px;
}
.about-me-header  svg {
    width: 24px;
    height: 24px;
    fill: var(--dark);
    position: relative;
    top: 25px;
}

.work {
  width: 100%;
  position: relative;
  margin-bottom: 3px;
}
.work  h3 {
  padding-left: 30px;
}
.work-edu-title {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 0 5px 30px;
  opacity: 0.9;
  font-size: 15px;
  text-transform: uppercase;
  font-family: 'Poppins', sans-serif;
  color: var(--dark);
  font-weight: 800;
}
.work-edu-description {
  padding-left: 5px;
}

.work-item,
.edu-item {
  margin-bottom: 10px;
  position: relative;
}
.white-circle {
  position: absolute;
  top: 5px;
  left: 0px;
  z-index: 3;
}

.cube {
    position: absolute;
  top: 6px;
  left: 2px;
  z-index: 3;
  background: var(--light);
  border: 2px solid var(--dark);
  width: 10px;
  height: 10px;

}


.work-edu-position {
  font-family: 'Poppins', sans-serif;
  color: var(--dark);
  font-weight: 400;
  padding-left: unset;
  font-size: 13.2px;
  z-index: 1;
  margin: 0;
}

.work-edu-position-time {
  display: flex;
  margin-left: 30px;
}

.cube-sm{
  position: relative;
  top: 7px;
  margin: 0 5px;
  width: 4px;
  height: 4px;
  background: var(--dark);
}
.work-edu-time {
  font-family: 'Poppins', sans-serif;
  color: var(--dark);
  font-size: 13.2px;
  font-weight: 400;
  background: var(--dark);
  color: var(--light);
  margin-left: 25px;
  height: 19px;
  padding: 0 10px;
}


.vertical-line-mask {
  position: absolute;
  top: 15px;
  left: 5px;
  width: 10px;
  height: 310%;
  background: var(--light);
}

.vertical-line {
  position: absolute;
  top: 16px;
  left: 6px;
  width: 2px;
  height: 100%;
  background: var(--dark);
  z-index: 0;
}

.work .work-item:nth-last-child(1) .vertical-line,
.education .edu-item:nth-last-child(1) .vertical-line {
  display: none !important;
}

.skills-item .delete-item-button,
.work-item .delete-item-button,
.edu-item .delete-item-button {
  position: absolute;
  right: 0;
  top: 0;
  display: none;
}


.education .add-item-button,
.work .add-item-button {
  display: none;
  position: absolute;
  z-index: 100;
  right: 0;
  bottom: -28px;
}
.skills:hover .add-item-button,
.skills-item:hover .delete-item-button,
.education:hover .add-item-button,
.work:hover .add-item-button,
.work-item:hover .delete-item-button,
.edu-item:hover .delete-item-button {
  display: block;
}



.skills {
  margin-top: 0px;
  position: relative;
}

.skills svg {
    width: 24px;
    height: 24px;
    fill: var(--dark);
    position: relative;
    top: 25px;
}
.skills  h3 {
     padding-left: 30px;
}
.skill {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 0 5px 0px;
  opacity: 0.9;
  font-size: 15px;
  text-transform: uppercase;
  font-family: 'Poppins', sans-serif;
  color: var(--dark);
  font-weight: 800;
}


.skills-items {
  display: grid;
  grid-template-columns: repeat(2,1fr);
  margin-left: 20px;
}



.skills-item {
  margin-bottom: 10px;
  position: relative;
  /* height: 50px; */
}

.skills-item .delete-item-button {
  position: absolute;
  right: 20px;
  top: 0;
  display: none;
}

.skills .add-item-button {
  position: relative;
  display: none;
  float: right;
}

.skill-input {
  width: 120px;
  margin: 4px 0px;
  display: none;
}

.skills-item:hover .skill-input {
  display: block;
}

.progress-bar {
  position: relative;
  width: 80%;
  height: 10px;
  border: 1px solid var(--dark);
  overflow: hidden;
}

.progress {
  content: '';
  width: 80%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: var(--dark);
}
#pdf {
  
  width: 795px;
  
  background: rgb(250, 250, 198);
}

.editable:hover {
  background: rgba(0, 183, 255, 0.705);
  cursor: pointer;
  padding: 0px 0px 0px 5px;
  position: relative;
}


.sectionTitle {
  font-size: 18px;
  text-transform: uppercase;
  font-family: 'Russo One', sans-serif;
  color: var(--dark);
  margin-bottom: 10px;
  border-bottom: 1px solid rgba(32, 38, 49, 0.2);
}

p {
  font-size: 15px;
  font-weight: 400;
  font-family: 'Poppins', sans-serif;
  color: var(--dark);
  opacity: 0.9;
}

main {
  display: flex;
}
.save-button,
.download-button {
  position: absolute;
  top: 15px;
  right: 20px;
  padding: 5px 20px;
  background: rgb(200, 8, 8);
  color: white;
  font-size: 20px;
  font-weight: 800;
  border-radius: 5px;
  margin-right: 20px;
  border: none;
}
.save-button {
  right: 220px;
  background: rgb(8, 139, 200);
}
.download-button:hover {
  background: rgb(218, 16, 16);

}
.workspace {
  width: 80%;
  padding: 10px 0 40px 40px;
  min-height: 100vh;
  background: rgb(141, 173, 214);
  background: url(/CV-Free/static/media/pattern.4da2c76c.jpg);
  background-position-x: left;
  background-repeat: repeat;
}
.sideBar {
  width: 20%;
  height: inherit;
  min-height: 100vh;
  /* overflow-y: scroll; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background: rgb(161, 159, 159);
  border: 2px solid gray;

}

.sideBar img {
  width: 180px;
  height: 260px;
  margin: 20px 0;
}

.active-template {
  border: 2px solid var(--blue-light);
/*   width: 180px;
  height: 260px;
  margin: 20px 0;
  background: url('../../img/template.png') center center /cover; */
}

.unavilable-template {
  opacity: 0.4;
}

.color {
  width: 22px;
  height: 22px;
  border: none;
  border-radius: 50%;
  margin: 5px;
  transition: 0.3s;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  outline: none;
}

.colors-pallete {
  border-radius: 15px;
  padding: 5px 20px;
  background: rgb(197, 197, 197);
}

.sideBar p {
  font-weight: 800;
  font-size: 18px;
}
.color:hover {
  transform: scale(1.1);

}

.color:nth-child(1) {
  background: rgb(32, 38, 49);
}

.color:nth-child(2) {
  background: rgb(11, 21, 53);
}

.color:nth-child(3) {
  background: rgb(34, 2, 71);
}

.color:nth-child(4) {
  background: rgb(0, 0, 0);
}

.color:nth-child(5) {
  background: rgb(53, 25, 25);
}

.color:nth-child(6) {
  background: rgb(66, 126, 10);
}

